//@flow


/**
 * Apply the correct article (a, an) to the given word.
 * @param  {String} word The input word
 * @return {String}      The input word prefixed by the article
 */
export default function articleize(word: string): string {
  const article = /^[aeiou]/.test(word) ? 'an' : 'a';
  return `${article} ${word}`;
}
